@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,600&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,

body ,

#root,
#root > div,
div[data-reactroot] {
  height: 60%;
  padding: 0;
  margin: 0;
}
.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}
.name-cell{
    text-transform: capitalize;
    background-color:palevioletred;
}
.datatable .col {
    cursor: pointer;
    background-color:rgb(71, 69, 71);
    color: white;
}

.datatable .col .pointer:after {
    font-size: 80%;
    content: "";
    margin-right: 18px;
}

.datatable .col .pointer:hover:after {
    font-size: 80%;
    content: "ðŸ”»";
    margin-right: 0;
}
.searchbox button{
    border: 1px solid rgb(114, 112, 114);
}
.searchbox button:hover {
    background-color: rgb(110, 109, 110);
    color:white;
}


.navbar {
    padding: 20px 25px;
    margin: 0 auto;

}

.navbar-collapse {
    margin: 0 auto;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.search-area {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
.data-area {
    margin: 0 auto;
    width: 90%;
}


